import Vue from 'vue'
import Header from '../../components/Header'
import { queryStockList, addMyStock, querySearchLogList, addSearchLog, delSearchLog } from '@/api/API'
import { InfiniteScroll, Spinner } from 'mint-ui'
Vue.use(InfiniteScroll)
Vue.component(Spinner.name, Spinner)

let timer = null
let searchLogTimer = null
export default {
  name: 'search',
  data () {
    return {
      isSearchLog: false,
      searchLogList: [],
      searchLogPage: 1,
      isLogEnd: false,
      logLoading: false,
      keywords: '',
      list: [],
      page: 1,
      pageSize: 20,
      isEnd: false,
      loading: false
    }
  },
  components: {
    Header
  },
  mounted () {
    if (this.$store.state.User) {
      this.isSearchLog = true
      this.searchLogList = []
      this.loadMoreSearchLog()
    }
  },
  watch: {
    keywords (val, oldVal) {
      this.list = []
      this.page = 1
      this.isEnd = false
      this.isLogEnd = false
      this.searchLogPage = 1
      this.searchLogList = []
      clearTimeout(timer)
      clearTimeout(searchLogTimer)
      if (val) {
        this.isSearchLog = false
        timer = setTimeout(() => {
          this.loadMore()
        }, 1000)
      } else {
        this.isSearchLog = true
        searchLogTimer = setTimeout(() => {
          this.loadMoreSearchLog()
        }, 1000)
      }
    }
  },
  methods: {
    loadMore () {
      if (this.isEnd || this.loading || !this.keywords) return
      this.loading = true
      this.getStockList()
    },
    loadMoreSearchLog () {
      if (this.isLogEnd || this.logLoading || this.keywords) return
      this.logLoading = true
      this.getSearchLogList()
    },
    async getSearchLogList () {
      let user = this.$store.state.User
      if (!user) return
      let res = await querySearchLogList(this.searchLogPage, this.pageSize, user.token)
      this.logLoading = false
      if (res.code === 200) {
        this.searchLogPage += 1
        let data = res.data || {}
        data = data.page_data || []
        if (data.length < this.pageSize) {
          this.isLogEnd = true
        }
        this.searchLogList = [...this.searchLogList, ...data]
      } else {
        window.EventBus.$emit('globalMessage', '获取失败')
      }
    },
    async getStockList () {
      this.loading = false
      let user = this.$store.state.User
      let res = await queryStockList(this.keywords, this.page, this.pageSize, user.token)
      if (res.code === 200) {
        this.page += 1
        let data = res.data || {}
        data = data.page_data || []
        if (data.length < this.pageSize) {
          this.isEnd = true
        }
        this.list = [...this.list, ...data]
      } else {
        window.EventBus.$emit('globalMessage', '获取失败')
      }
    },
    onShow (stock, isSearch) {
      if (isSearch) {
        this.addSearchLog(stock)
      }
      this.$router.push({name: 'Stock', params: {stock_code: stock.stock_code}})
    },
    async addStock (stock) {
      if (stock.focus === 1) return
      let user = this.$store.state.User || {}
      let res = await addMyStock(stock.stock_code, user.token)
      if (res.code === 200) {
        // eslint-disable-next-line
        stock.focus = 1
      }
      window.EventBus.$emit('globalMessage', res.msg)
      this.addSearchLog(stock)
    },
    async addSearchLog (stock) {
      let user = this.$store.state.User || {}
      if (user.token) {
        let res = await addSearchLog(stock.stock_code, user.token)
        console.log(res)
      }
    },
    async delSearchLog () {
      let user = this.$store.state.User || {}
      let res = await delSearchLog(user.token)
      if (res.code === 200) {
        this.searchLogList = []
      }
      window.EventBus.$emit('globalMessage', res.msg)
    }
  }
}



